import axios from 'axios';
import { FETCH_USER } from './types';

export const fetchUser = () => {
    return function(dispatch) {
        axios.get('/auth/api/current_user')
            .then(res => dispatch({
                type: FETCH_USER,
                payload: res.data
            }));
    };
};

export const addIncome = values => {
    return { type: 'ADD_INCOME'};
};